import mongoose, { Schema, Document } from "mongoose";

export interface IServicePricing extends Document {
    serviceGroup: string;
    serviceDetail: string;
    package1?: string;
    package2?: string;
    package3?: string;
    package4?: string;
    package5?: string;
    unitPrice?: number;
    isActive: boolean;
    createdAt?: Date;
    updatedAt?: Date;
}

const ServicePricingSchema: Schema = new Schema(
    {
        serviceGroup: {
            type: String,
            required: true,
            trim: true,
        },
        serviceDetail: {
            type: String,
            required: true,
            trim: true,
        },
        package1: {
            type: String,
            trim: true,
        },
        package2: {
            type: String,
            trim: true,
        },
        package3: {
            type: String,
            trim: true,
        },
        package4: {
            type: String,
            trim: true,
        },
        package5: {
            type: String,
            trim: true,
        },
        unitPrice: {
            type: Number,
            min: 0,
        },
        isActive: {
            type: Boolean,
            default: true,
        },
    },
    {
        timestamps: true,
        collection: "CAI_DAT_GIA",
    },
);

export default mongoose.models.ServicePricing ||
    mongoose.model<IServicePricing>("ServicePricing", ServicePricingSchema);
